@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    background-color: #181818;
}

.f-v {
    @apply flex flex-col;
}

.f-h {
    @apply flex flex-row;
}

.form-label {
    @apply mb-1 text-sm text-zinc-500;
}

.form-input {
    @apply px-2 py-1 rounded-md border-0 text-sm bg-[#181818] text-white;
}

.form-input:disabled {
    @apply text-white bg-[#1F1E1F] border border-[#2A282B];
}

.form-select {
    @apply form-input;
    /* border-right: 4px solid transparent; */
}

.button {
    @apply px-4 py-1 rounded-full shadow-md text-xs text-white bg-[#494849] hover:bg-[#656365];

    /* &:disabled {
        @apply text-zinc-400;
    } */
}

/*расчет 4 строк в названии позиции*/
.fourrow {
    margin: 0;
    /* Убираем внешний отступ */
    -webkit-line-clamp: 4;
    /* Число отображаемых строк */
    display: -webkit-box;
    /* Включаем флексбоксы */
    -webkit-box-orient: vertical;
    /* Вертикальная ориентация */
    overflow: hidden;
    /* Обрезаем всё за пределами блока */
}