.org-input {
    @apply w-full h-7 px-2 rounded-md border-0 text-sm bg-[#181818] text-white focus:outline-none focus:ring-1 focus:ring-[#FFFFFF]
};

.org-input:disabled {
    @apply w-full h-7 px-2 rounded-md border border-[#2A282B] text-sm text-white bg-[#1F1E1F] focus:outline-none focus:ring-1 focus:ring-[#FFFFFF]
}

.switch {
    width: 43px;
    height: 25px;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    border: solid 2px #33303300;
  }
  
  .switch-on {
    background-color: #187A33;
    color:#187A33;
  }
  
  .switch-off {
    background-color: #656365;
    color:#656365
  }
  
  .switch-disabled {
    background-color: #242225;
    border: solid 2px #333033;
    cursor: default;
  }
  
  .switch-toggle {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.3s ease;
  }
  
  .toggle-on {
    transform: translateX(13px);
  }
  
  .toggle-off {
    transform: translateX(-1px);
  }

  .toggle-on-disabled {
    background-color: #187A33; 
  }

  .toggle-off-disabled {
    background-color: #8A8A8A; 
  }